.c-SectionLoader {

    display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(0,0,0,0.3);  
    width: 100%;  
    height: 100%;  
    position: relative;  
    top:0px;  
    left:0px;  
    z-index: 10;
  
    .c-SectionLoader__lds-ripple {
		display: inline-block;
		position: absolute;
		width: 80px;
		height: 80px;
		div {
			position: absolute;
			border: 4px solid #fff;
			opacity: 1;
			border-radius: 50%;
			animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
			&:nth-child(2) {
				animation-delay: -0.5s;
			}
		}
	}
	
	@keyframes lds-ripple {
		0% {
			top: 36px;
			left: 36px;
			width: 0;
			height: 0;
			opacity: 1;
		}
		100% {
			top: 0px;
			left: 0px;
			width: 72px;
			height: 72px;
			opacity: 0;
		}
	}
}
